import webSocketService from './WebSocket';
import { WS_MESSAGE_TYPES, WS_ACTIONS } from '../constants/websocket';
import { FileUploadMessage, FileDownloadMessage } from '../types/websocket';
import { 
    generateFileId, 
    splitFileIntoChunks, 
    assembleChunks, 
    getMimeType, 
    downloadFile 
} from '../utils/fileUtils';

class FileService {
    private static instance: FileService;
    private ws = webSocketService;
    private fileDownloadCallbacks: Map<string, (data: ArrayBuffer) => void> = new Map();
    private fileChunks: Map<string, string[]> = new Map();

    private constructor() {
        this.setupMessageHandlers();
    }

    public static getInstance(): FileService {
        if (!FileService.instance) {
            FileService.instance = new FileService();
        }
        return FileService.instance;
    }

    private setupMessageHandlers(): void {
        this.ws.addMessageHandler(WS_MESSAGE_TYPES.MESSENGER, (message) => {
            if (message.action === WS_ACTIONS.DOWNLOAD_FILE) {
                const downloadMessage = message as FileDownloadMessage;
                this.handleFileChunk(downloadMessage);
            }
        });
    }

    private handleFileChunk(message: FileDownloadMessage): void {
        const { fileId, chunkNumber, totalChunks, chunkData } = message.data;
        
        if (!this.fileChunks.has(fileId)) {
            this.fileChunks.set(fileId, new Array(totalChunks).fill(''));
        }

        const chunks = this.fileChunks.get(fileId)!;
        chunks[chunkNumber - 1] = chunkData;

        // ,    
        if (!chunks.includes('')) {
            const fileData = assembleChunks(chunks);
            const callback = this.fileDownloadCallbacks.get(fileId);
            if (callback) {
                callback(fileData);
                this.fileDownloadCallbacks.delete(fileId);
            }
            this.fileChunks.delete(fileId);
        }
    }

    public async uploadFile(file: File): Promise<string> {
        const fileId = generateFileId();
        const chunks = await splitFileIntoChunks(file);
        
        //    
        const uploadMessage: FileUploadMessage = {
            type: WS_MESSAGE_TYPES.MESSENGER,
            action: WS_ACTIONS.UPLOAD_FILE,
            data: {
                fileId,
                fileName: file.name,
                fileSize: file.size,
                mimeType: getMimeType(file.name)
            }
        };
        await this.ws.send(uploadMessage);

        //   
        for (let i = 0; i < chunks.length; i++) {
            const downloadMessage: FileDownloadMessage = {
                type: WS_MESSAGE_TYPES.MESSENGER,
                action: WS_ACTIONS.DOWNLOAD_FILE,
                data: {
                    fileId,
                    chunkNumber: i + 1,
                    totalChunks: chunks.length,
                    chunkData: chunks[i]
                }
            };
            await this.ws.send(downloadMessage);
        }

        return fileId;
    }

    public downloadFile(fileId: string, fileName: string): Promise<void> {
        return new Promise((resolve, reject) => {
            this.fileDownloadCallbacks.set(fileId, (data: ArrayBuffer) => {
                try {
                    downloadFile(data, fileName, getMimeType(fileName));
                    resolve();
                } catch (error) {
                    reject(error);
                }
            });

            //  
            const downloadMessage: FileDownloadMessage = {
                type: WS_MESSAGE_TYPES.MESSENGER,
                action: WS_ACTIONS.DOWNLOAD_FILE,
                data: {
                    fileId,
                    chunkNumber: 0,
                    totalChunks: 0,
                    chunkData: ''
                }
            };
            this.ws.send(downloadMessage);
        });
    }
}

export default FileService; 