import React, { useState, useContext } from 'react';
import {
  View,
  TextInput,
  TouchableOpacity,
  Text,
  StyleSheet,
  Alert,
  ActivityIndicator,
  Platform,
} from 'react-native';
import { authAPI } from '../services/api';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { CommonActions, useNavigation } from '@react-navigation/native';
import { AuthContext } from '../navigation';
import HCaptcha from '@hcaptcha/react-hcaptcha';
import ErrorMessage from '../components/ErrorMessage';

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    padding: 20,
    backgroundColor: '#fff',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 30,
    textAlign: 'center',
  },
  input: {
    height: 50,
    borderWidth: 1,
    borderColor: '#ddd',
    borderRadius: 8,
    paddingHorizontal: 15,
    marginBottom: 15,
    fontSize: 16,
    backgroundColor: '#fff',
    ...(Platform.OS === 'web' && {
      outlineStyle: 'none',
      ':focus': {
        borderColor: '#007AFF',
      },
    }),
  },
  buttonContainer: {
    pointerEvents: 'auto',
    marginBottom: 15,
  },
  button: {
    height: 50,
    backgroundColor: '#007AFF',
    borderRadius: 8,
    justifyContent: 'center',
    alignItems: 'center',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      ':hover': {
        backgroundColor: '#0056b3',
      },
    }),
  },
  buttonDisabled: {
    backgroundColor: '#ccc',
  },
  buttonText: {
    color: 'white',
    fontSize: 16,
    fontWeight: '600',
  },
  switchText: {
    color: '#007AFF',
    textAlign: 'center',
    marginTop: 15,
    fontSize: 16,
  },
  stepText: {
    fontSize: 18,
    marginBottom: 15,
    textAlign: 'center',
  },
});

const LoginScreen = () => {
  const navigation = useNavigation();
  const { setIsLoggedIn } = useContext(AuthContext);
  const [isRegistering, setIsRegistering] = useState(false);
  const [loading, setLoading] = useState(false);
  
  // Login state
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loginError, setLoginError] = useState('');

  // Registration state
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [regEmail, setRegEmail] = useState('');
  const [regPassword, setRegPassword] = useState('');
  const [captchaToken, setCaptchaToken] = useState('');
  const [registrationError, setRegistrationError] = useState('');

  const handleLogin = async () => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      setLoginError(' ');
      return;
    }
    if (!emailRegex.test(email)) {
      setLoginError('  email ');
      return;
    }
    if (!password) {
      setLoginError(' ');
      return;
    }

    try {
      setLoading(true);
      const response = await authAPI.login(email.trim(), password);
      
      if (response?.status === 'success' && response?.S_KEY) {
        await AsyncStorage.setItem('S-KEY', response.S_KEY);
        setIsLoggedIn(true);
      } else {
        setLoginError(response?.text || ' email  ');
      }
    } catch (error) {
      console.error('Login error:', error);
      setLoginError('-   ');
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = async () => {
    setRegistrationError('');
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    
    // 
    if (!name.trim()) {
      setRegistrationError(' ');
      return;
    }
    if (!username.trim()) {
      setRegistrationError('  ');
      return;
    }
    if (!regEmail.trim()) {
      setRegistrationError(' email');
      return;
    }
    if (!emailRegex.test(regEmail.trim())) {
      setRegistrationError('  email ');
      return;
    }
    if (!regPassword.trim()) {
      setRegistrationError(' ');
      return;
    }
    if (!captchaToken) {
      setRegistrationError(' ');
      return;
    }

    try {
      setLoading(true);

      const formData = new FormData();
      formData.append('name', name.trim());
      formData.append('username', username.trim());
      formData.append('email', regEmail.trim());
      formData.append('password', regPassword.trim());
      formData.append('accept', 'true');
      formData.append('h_captcha', captchaToken);

      const response = await fetch('https://elemsocial.com/auth/register', {
        method: 'POST',
        headers: {
          'api': 'true',
          'Origin': 'https://elemsocial.com',
        },
        body: formData,
      });

      const data = await response.json();
      
      if (data.status === 'success') {
        await AsyncStorage.setItem('S-KEY', data.S_KEY);
        setIsLoggedIn(true);
      } else {
        setRegistrationError(data.text || '  ');
      }
    } catch (error) {
      console.error('Registration error:', error);
      setRegistrationError('-   ');
    } finally {
      setLoading(false);
    }
  };

  const onCaptchaVerify = (token: string) => {
    setCaptchaToken(token);
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Sigma Element Client</Text>
      
      {isRegistering ? (
        <>
          <TextInput
            style={styles.input}
            placeholder=""
            value={name}
            onChangeText={setName}
            editable={!loading}
          />
          <TextInput
            style={styles.input}
            placeholder=" "
            value={username}
            onChangeText={setUsername}
            editable={!loading}
            autoCapitalize="none"
          />
          <TextInput
            style={styles.input}
            placeholder="Email"
            value={regEmail}
            onChangeText={setRegEmail}
            editable={!loading}
            keyboardType="email-address"
            autoCapitalize="none"
          />
          <TextInput
            style={styles.input}
            placeholder=""
            value={regPassword}
            onChangeText={setRegPassword}
            secureTextEntry
            editable={!loading}
          />
          <HCaptcha
            sitekey="29c6b1c2-7e78-43ec-8bf8-5de49c58c54a"
            onVerify={onCaptchaVerify}
          />
          <ErrorMessage 
            message={registrationError} 
            onClose={() => setRegistrationError('')}
          />
          <View style={styles.buttonContainer}>
            <TouchableOpacity
              style={[styles.button, loading && styles.buttonDisabled]}
              onPress={handleRegister}
              disabled={loading}
            >
              {loading ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={styles.buttonText}></Text>
              )}
            </TouchableOpacity>
          </View>
          <TouchableOpacity onPress={() => {
            setIsRegistering(false);
            setRegistrationError('');
            setLoginError('');
          }}>
            <Text style={styles.switchText}>  ? </Text>
          </TouchableOpacity>
        </>
      ) : (
        <>
          <TextInput
            style={styles.input}
            placeholder=" "
            value={email}
            onChangeText={setEmail}
            autoCapitalize="none"
            keyboardType="email-address"
            editable={!loading}
          />
          <TextInput
            style={styles.input}
            placeholder=""
            value={password}
            onChangeText={setPassword}
            secureTextEntry
            editable={!loading}
          />
          <ErrorMessage 
            message={loginError} 
            onClose={() => setLoginError('')}
          />
          <View style={styles.buttonContainer}>
            <TouchableOpacity
              style={[styles.button, loading && styles.buttonDisabled]}
              onPress={handleLogin}
              disabled={loading}
            >
              {loading ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={styles.buttonText}></Text>
              )}
            </TouchableOpacity>
          </View>
          <TouchableOpacity onPress={() => {
            setIsRegistering(true);
            setRegistrationError('');
            setLoginError('');
          }}>
            <Text style={styles.switchText}> ? </Text>
          </TouchableOpacity>
        </>
      )}
    </View>
  );
};

export default LoginScreen;
