import React, { useEffect, useState, useRef } from 'react';
import {
  View,
  Text,
  Modal,
  StyleSheet,
  TouchableOpacity,
  Image,
  ActivityIndicator,
  Dimensions,
  Animated,
  Platform,
  ScrollView,
  TouchableWithoutFeedback
} from 'react-native';
import { profileAPI, ProfileData } from '../services/api';
import LetterAvatar from './LetterAvatar';
import LinkText from './LinkText';

const { width: SCREEN_WIDTH, height: SCREEN_HEIGHT } = Dimensions.get('window');
const MODAL_PADDING = 20;
const MODAL_WIDTH = Platform.select({
  web: Math.min(600, SCREEN_WIDTH - MODAL_PADDING * 2),
  default: SCREEN_WIDTH - MODAL_PADDING * 2
});

interface ProfileModalProps {
  visible: boolean;
  username: string;
  onClose: () => void;
}

const ProfileModal: React.FC<ProfileModalProps> = ({ visible, username, onClose }) => {
  const [profile, setProfile] = useState<ProfileData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  //  
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const scaleAnim = useRef(new Animated.Value(0.9)).current;
  const translateYAnim = useRef(new Animated.Value(50)).current;

  useEffect(() => {
    if (visible) {
      setLoading(true);
      setError('');
      loadProfile();
      
      //   
      Animated.parallel([
        Animated.timing(fadeAnim, {
          toValue: 1,
          duration: 300,
          useNativeDriver: true,
        }),
        Animated.timing(scaleAnim, {
          toValue: 1,
          duration: 300,
          useNativeDriver: true,
        }),
        Animated.timing(translateYAnim, {
          toValue: 0,
          duration: 300,
          useNativeDriver: true,
        })
      ]).start();
    } else {
      //  
      Animated.parallel([
        Animated.timing(fadeAnim, {
          toValue: 0,
          duration: 250,
          useNativeDriver: true,
        }),
        Animated.timing(scaleAnim, {
          toValue: 0.9,
          duration: 250,
          useNativeDriver: true,
        }),
        Animated.timing(translateYAnim, {
          toValue: 50,
          duration: 250,
          useNativeDriver: true,
        })
      ]).start(() => {
        setProfile(null);
        setError('');
      });
    }
  }, [visible, username]);

  const handleClose = () => {
    //   
    Animated.parallel([
      Animated.timing(fadeAnim, {
        toValue: 0,
        duration: 250,
        useNativeDriver: true,
      }),
      Animated.timing(scaleAnim, {
        toValue: 0.9,
        duration: 250,
        useNativeDriver: true,
      }),
      Animated.timing(translateYAnim, {
        toValue: 50,
        duration: 250,
        useNativeDriver: true,
      })
    ]).start(() => {
      onClose();
    });
  };

  const loadProfile = async () => {
    try {
      setLoading(true);
      setError('');
      const data = await profileAPI.getProfile(username);
      
      //  URL   
      if (data.avatar) {
        data.avatar = `https://elemsocial.com/Content/Avatars/${data.avatar}`;
      }
      if (data.cover) {
        data.cover = `https://elemsocial.com/Content/Covers/${data.cover}`;
      }
      
      setProfile(data);
    } catch (error) {
      console.error('  :', error);
      setError('   ');
    } finally {
      setLoading(false);
    }
  };

  const renderStats = () => {
    if (!profile) return null;

    const stats = [
      { label: '', value: profile.posts },
      { label: '', value: profile.subscribers },
      { label: '', value: profile.subscriptions }
    ];

    return (
      <View style={styles.stats}>
        {stats.map((stat) => (
          <View key={stat.label} style={styles.statItem}>
            <Text style={styles.statNumber}>
              {stat.value?.toLocaleString() || '0'}
            </Text>
            <Text style={styles.statLabel}>{stat.label}</Text>
          </View>
        ))}
      </View>
    );
  };

  return (
    <Modal
      visible={visible}
      transparent={true}
      onRequestClose={handleClose}
      animationType="none"
      statusBarTranslucent
    >
      <Animated.View 
        style={[
          styles.modalOverlay,
          {
            opacity: fadeAnim,
          }
        ]}
      >
        <TouchableWithoutFeedback onPress={handleClose}>
          <View style={StyleSheet.absoluteFill} />
        </TouchableWithoutFeedback>
        
        <Animated.View 
          style={[
            styles.modalContent,
            {
              opacity: fadeAnim,
              transform: [
                { scale: scaleAnim },
                { translateY: translateYAnim }
              ]
            }
          ]}
        >
          <TouchableOpacity 
            style={styles.closeButton} 
            onPress={handleClose}
            hitSlop={{ top: 10, right: 10, bottom: 10, left: 10 }}
          >
            <Text style={styles.closeButtonText}></Text>
          </TouchableOpacity>

          <ScrollView 
            style={styles.scrollView}
            bounces={false}
            showsVerticalScrollIndicator={false}
          >
            {loading ? (
              <ActivityIndicator size="large" color="#007AFF" style={styles.loader} />
            ) : error ? (
              <Text style={styles.errorText}>{error}</Text>
            ) : profile ? (
              <View style={styles.profileContent}>
                <View style={styles.coverContainer}>
                  {profile.cover ? (
                    <Image 
                      source={{ uri: profile.cover }} 
                      style={styles.coverImage}
                      resizeMode="cover"
                    />
                  ) : (
                    <View style={styles.defaultCover} />
                  )}
                </View>

                <View style={[styles.avatarContainer, styles.avatarWithCover]}>
                  {profile.avatar && profile.avatar !== 'None' ? (
                    <Image 
                      source={{ uri: profile.avatar }} 
                      style={styles.avatar}
                    />
                  ) : (
                    <LetterAvatar 
                      name={profile.name || profile.username} 
                      size={80}
                    />
                  )}
                </View>

                <View style={styles.nameRow}>
                  <Text style={styles.name}>{profile.name}</Text>
                  {profile.icons && profile.icons.includes('VERIFY') && (
                    <Image
                      source={require('../assets/verify.png')}
                      style={styles.verifiedBadge}
                    />
                  )}
                </View>
                <Text style={styles.username}>@{profile.username}</Text>
                
                {profile.description && (
                  <LinkText text={profile.description} style={styles.description} />
                )}

                {renderStats()}

                {profile.icons && profile.icons.length > 0 && (
                  <View style={styles.iconsContainer}>
                    {profile.icons.filter(icon => icon !== 'VERIFY').map((icon, index) => (
                      <Image
                        key={index}
                        source={{ uri: `https://elemsocial.com/Content/Icons/${icon}` }}
                        style={styles.icon}
                      />
                    ))}
                  </View>
                )}
              </View>
            ) : null}
          </ScrollView>
        </Animated.View>
      </Animated.View>
    </Modal>
  );
};

const styles = StyleSheet.create({
  modalOverlay: {
    flex: 1,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  modalContent: {
    position: 'relative',
    backgroundColor: 'white',
    borderRadius: 15,
    width: MODAL_WIDTH,
    maxHeight: SCREEN_HEIGHT - MODAL_PADDING * 2,
    marginHorizontal: MODAL_PADDING,
    overflow: 'hidden',
    ...Platform.select({
      ios: {
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.25,
        shadowRadius: 4,
      },
      android: {
        elevation: 5,
      },
    }),
  },
  scrollView: {
    maxHeight: SCREEN_HEIGHT - MODAL_PADDING * 2,
  },
  closeButton: {
    position: 'absolute',
    right: 15,
    top: 15,
    zIndex: 2,
    width: 30,
    height: 30,
    borderRadius: 15,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  closeButtonText: {
    fontSize: 20,
    color: '#fff',
    fontWeight: 'bold',
  },
  loader: {
    padding: 20,
  },
  errorText: {
    color: 'red',
    textAlign: 'center',
    padding: 20,
  },
  profileContent: {
    alignItems: 'center',
  },
  coverContainer: {
    width: '100%',
    height: Math.min(200, SCREEN_HEIGHT * 0.25),
    backgroundColor: '#f0f0f0',
  },
  coverImage: {
    width: '100%',
    height: '100%',
  },
  defaultCover: {
    width: '100%',
    height: '100%',
    backgroundColor: '#f0f0f0',
  },
  avatarContainer: {
    width: Math.min(100, SCREEN_WIDTH * 0.2),
    height: Math.min(100, SCREEN_WIDTH * 0.2),
    marginTop: -Math.min(50, SCREEN_WIDTH * 0.1),
    marginBottom: 10,
    borderRadius: Math.min(50, SCREEN_WIDTH * 0.1),
    borderWidth: 4,
    borderColor: '#fff',
    overflow: 'hidden',
    backgroundColor: '#f0f0f0',
  },
  avatarWithCover: {
    marginTop: -Math.min(50, SCREEN_WIDTH * 0.1),
  },
  avatar: {
    width: '100%',
    height: '100%',
  },
  nameRow: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 4,
    marginBottom: 4,
  },
  name: {
    fontSize: 20,
    fontWeight: 'bold',
    color: '#000',
  },
  username: {
    fontSize: 16,
    color: '#666',
    marginBottom: 10,
  },
  description: {
    fontSize: 14,
    color: '#444',
    textAlign: 'center',
    marginHorizontal: 20,
    marginBottom: 15,
  },
  stats: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    width: '100%',
    paddingVertical: 15,
    borderTopWidth: 1,
    borderBottomWidth: 1,
    borderColor: '#eee',
  },
  statItem: {
    alignItems: 'center',
    flex: 1,
  },
  statNumber: {
    fontSize: 18,
    fontWeight: 'bold',
  },
  statLabel: {
    fontSize: 12,
    color: '#666',
    marginTop: 4,
  },
  iconsContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    padding: 10,
    gap: 8,
  },
  icon: {
    width: 24,
    height: 24,
  },
  verifiedBadge: {
    width: 20,
    height: 20,
  },
});

export default ProfileModal; 