import React, { useState, useRef, useEffect } from 'react';
import {
  View,
  StyleSheet,
  TouchableOpacity,
  Animated,
  Text,
  ActivityIndicator,
  Platform,
  Dimensions,
  TouchableWithoutFeedback,
} from 'react-native';
import { Video, ResizeMode, AVPlaybackStatus } from 'expo-av';
import { MaterialIcons } from '@expo/vector-icons';

interface CustomVideoPlayerProps {
  uri: string;
  style?: any;
  onPress?: () => void;
  shouldPlay?: boolean;
}

const { width: SCREEN_WIDTH } = Dimensions.get('window');
const CONTROLS_TIMEOUT = 3000;

const CustomVideoPlayer: React.FC<CustomVideoPlayerProps> = ({
  uri,
  style,
  onPress,
  shouldPlay = false,
}) => {
  const [status, setStatus] = useState<AVPlaybackStatus | null>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [showControls, setShowControls] = useState(true);
  const [isSeeking, setIsSeeking] = useState(false);
  const [seekPosition, setSeekPosition] = useState(0);
  
  const videoRef = useRef<Video>(null);
  const controlsTimeoutRef = useRef<NodeJS.Timeout>();
  const controlsOpacity = useRef(new Animated.Value(1)).current;
  const playButtonScale = useRef(new Animated.Value(1)).current;

  useEffect(() => {
    return () => {
      if (controlsTimeoutRef.current) {
        clearTimeout(controlsTimeoutRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (shouldPlay && !isPlaying && status?.isLoaded) {
      handlePlayPause();
    }
  }, [shouldPlay, status]);

  const resetControlsTimeout = () => {
    if (controlsTimeoutRef.current) {
      clearTimeout(controlsTimeoutRef.current);
    }

    if (isPlaying) {
      controlsTimeoutRef.current = setTimeout(() => {
        Animated.timing(controlsOpacity, {
          toValue: 0,
          duration: 300,
          useNativeDriver: true,
        }).start(() => setShowControls(false));
      }, CONTROLS_TIMEOUT);
    }
  };

  const showControlsHandler = () => {
    setShowControls(true);
    Animated.timing(controlsOpacity, {
      toValue: 1,
      duration: 200,
      useNativeDriver: true,
    }).start();
    resetControlsTimeout();
  };

  const handleVideoPress = () => {
    if (!showControls) {
      showControlsHandler();
    } else if (onPress) {
      onPress();
    }
  };

  const handlePlayPause = async () => {
    if (!videoRef.current || !status?.isLoaded) return;

    try {
      if (isPlaying) {
        await videoRef.current.pauseAsync();
        showControlsHandler();
      } else {
        await videoRef.current.playAsync();
        resetControlsTimeout();
      }
      setIsPlaying(!isPlaying);

      Animated.sequence([
        Animated.timing(playButtonScale, {
          toValue: 0.8,
          duration: 100,
          useNativeDriver: true,
        }),
        Animated.spring(playButtonScale, {
          toValue: 1,
          friction: 3,
          tension: 40,
          useNativeDriver: true,
        }),
      ]).start();
    } catch (error) {
      console.error('Error toggling play state:', error);
    }
  };

  const handleVideoStatus = (newStatus: AVPlaybackStatus) => {
    if (!newStatus.isLoaded) return;
    
    setStatus(newStatus);
    if (!isSeeking) {
      setSeekPosition(newStatus.positionMillis / (newStatus.durationMillis || 1));
    }
  };

  const handleSeekBarPress = (event: any) => {
    if (!status?.isLoaded) return;

    const { locationX } = event.nativeEvent;
    const progress = Math.max(0, Math.min(1, locationX / (SCREEN_WIDTH - 32)));
    setIsSeeking(true);
    setSeekPosition(progress);

    if (videoRef.current) {
      videoRef.current.setPositionAsync(progress * (status.durationMillis || 0))
        .then(() => setIsSeeking(false))
        .catch(console.error);
    }
  };

  const formatTime = (millis: number): string => {
    if (!millis || isNaN(millis)) return '0:00';
    const totalSeconds = Math.floor(millis / 1000);
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  const getCurrentTime = (): string => {
    if (!status?.isLoaded) return '0:00';
    return formatTime(status.positionMillis);
  };

  const getDuration = (): string => {
    if (!status?.isLoaded) return '0:00';
    return formatTime(status.durationMillis || 0);
  };

  return (
    <View style={[styles.container, style]}>
      <TouchableWithoutFeedback onPress={handleVideoPress}>
        <View style={styles.videoContainer}>
          <Video
            ref={videoRef}
            source={{ uri }}
            style={StyleSheet.absoluteFill}
            resizeMode={ResizeMode.CONTAIN}
            onPlaybackStatusUpdate={handleVideoStatus}
            shouldPlay={shouldPlay}
            isLooping
          />

          {(!status?.isLoaded || status?.isBuffering) && (
            <View style={styles.loadingContainer}>
              <ActivityIndicator size="large" color="#fff" />
            </View>
          )}

          {showControls && (
            <Animated.View style={[styles.controls, { opacity: controlsOpacity }]}>  
              <TouchableOpacity
                style={styles.playButtonContainer}
                onPress={handlePlayPause}
              >
                <Animated.View style={{ transform: [{ scale: playButtonScale }] }}>
                  <MaterialIcons
                    name={isPlaying ? 'pause' : 'play-arrow'}
                    size={50}
                    color="#fff"
                  />
                </Animated.View>
              </TouchableOpacity>

              <View style={styles.bottomControls}>
                <TouchableWithoutFeedback onPress={handleSeekBarPress}>
                  <View style={styles.progressBar}>
                    <View style={styles.progressBackground} />
                    <View
                      style={[
                        styles.progressFill,
                        { width: `${seekPosition * 100}%` },
                      ]}
                    />
                    <View
                      style={[
                        styles.seekHandle,
                        { left: `${seekPosition * 100}%` },
                      ]}
                    />
                  </View>
                </TouchableWithoutFeedback>

                <View style={styles.timeContainer}>
                  <Text style={styles.timeText}>{getCurrentTime()}</Text>
                  <Text style={styles.timeText}>{getDuration()}</Text>
                </View>
              </View>
            </Animated.View>
          )}
        </View>
      </TouchableWithoutFeedback>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#000',
    overflow: 'hidden',
    borderRadius: 12,
  },
  videoContainer: {
    aspectRatio: 16 / 9,
    backgroundColor: '#000',
    justifyContent: 'center',
    alignItems: 'center',
  },
  loadingContainer: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
  },
  controls: {
    ...StyleSheet.absoluteFillObject,
    padding: 16,
    justifyContent: 'space-between',
    backgroundColor: 'rgba(0, 0, 0, 0.4)',
  },
  playButtonContainer: {
    alignSelf: 'center',
    width: 80,
    height: 80,
    borderRadius: 40,
    backgroundColor: 'rgba(0, 0, 0, 0.6)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  bottomControls: {
    width: '100%',
  },
  progressBar: {
    height: 4,
    width: '100%',
    backgroundColor: 'rgba(255, 255, 255, 0.3)',
    borderRadius: 2,
    overflow: 'visible',
    position: 'relative',
  },
  progressBackground: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(255, 255, 255, 0.3)',
  },
  progressFill: {
    height: '100%',
    backgroundColor: '#007AFF',
    borderRadius: 2,
  },
  seekHandle: {
    position: 'absolute',
    top: -6,
    marginLeft: -8,
    width: 16,
    height: 16,
    borderRadius: 8,
    backgroundColor: '#fff',
    ...Platform.select({
      ios: {
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.25,
        shadowRadius: 4,
      },
      android: {
        elevation: 5,
      },
    }),
  },
  timeContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginTop: 8,
  },
  timeText: {
    color: '#fff',
    fontSize: 12,
    fontWeight: '600',
    textShadowColor: 'rgba(0, 0, 0, 0.5)',
    textShadowOffset: { width: 1, height: 1 },
    textShadowRadius: 2,
  },
});

export default CustomVideoPlayer;