import React, { useEffect, useState, useRef } from 'react';
import { View, Text, StyleSheet, Animated } from 'react-native';
import webSocketService from '../services/WebSocket';

const WebSocketStatus: React.FC = () => {
    const [status, setStatus] = useState<'connecting' | 'connected' | 'error'>('connecting');
    const [errorMessage, setErrorMessage] = useState<string | null>(null);
    const slideAnim = useRef(new Animated.Value(-100)).current;
    const opacityAnim = useRef(new Animated.Value(0)).current;
    const wasConnected = useRef(false);

    useEffect(() => {
        const checkConnection = () => {
            if (webSocketService.connectionStatus && webSocketService.ready) {
                if (!wasConnected.current) {
                    setStatus('connected');
                    wasConnected.current = true;
                    //       2 
                    setTimeout(hideNotification, 2000);
                }
            } else if (wasConnected.current) {
                //       
                setStatus('error');
                setErrorMessage(' ');
                wasConnected.current = false;
                showNotification();
            }
        };

        const showNotification = () => {
            Animated.parallel([
                Animated.timing(slideAnim, {
                    toValue: 0,
                    duration: 300,
                    useNativeDriver: true,
                }),
                Animated.timing(opacityAnim, {
                    toValue: 1,
                    duration: 300,
                    useNativeDriver: true,
                }),
            ]).start();
        };

        const interval = setInterval(checkConnection, 1000);

        const timeout = setTimeout(() => {
            if (status === 'connecting' && !wasConnected.current) {
                setStatus('error');
                setErrorMessage('   ');
                showNotification();
            }
        }, 15000);

        if (status === 'connecting' && !wasConnected.current) {
            showNotification();
        }

        return () => {
            clearInterval(interval);
            clearTimeout(timeout);
        };
    }, [status]);

    const hideNotification = () => {
        Animated.parallel([
            Animated.timing(slideAnim, {
                toValue: -100,
                duration: 300,
                useNativeDriver: true,
            }),
            Animated.timing(opacityAnim, {
                toValue: 0,
                duration: 300,
                useNativeDriver: true,
            }),
        ]).start();
    };

    const getMessage = () => {
        switch (status) {
            case 'connecting':
                return ' ...';
            case 'connected':
                return ' ';
            case 'error':
                return errorMessage || ' ';
        }
    };

    if (status === 'connected' && !opacityAnim) return null;

    return (
        <Animated.View 
            style={[
                styles.container,
                {
                    transform: [{ translateY: slideAnim }],
                    opacity: opacityAnim,
                }
            ]}
        >
            <View style={[
                styles.content,
                status === 'error' && styles.errorContent,
                status === 'connected' && styles.successContent
            ]}>
                <Text style={styles.text}>{getMessage()}</Text>
            </View>
        </Animated.View>
    );
};

const styles = StyleSheet.create({
    container: {
        position: 'absolute',
        top: 10,
        left: 10,
        right: 10,
        zIndex: 1000,
        borderRadius: 8,
        overflow: 'hidden',
    },
    content: {
        backgroundColor: '#333',
        padding: 14,
        alignItems: 'center',
        opacity: 0.9,
        shadowColor: '#000',
        shadowOffset: {
            width: 0,
            height: 2,
        },
        shadowOpacity: 0.25,
        shadowRadius: 3.84,
        elevation: 5,
    },
    errorContent: {
        backgroundColor: '#DC3545',
    },
    successContent: {
        backgroundColor: '#28A745',
    },
    text: {
        color: '#fff',
        fontSize: 15,
        fontWeight: '500',
    }
});

export default WebSocketStatus; 