import React, { useState, useRef } from 'react';
import {
    View,
    TextInput,
    TouchableOpacity,
    StyleSheet,
    ActivityIndicator,
    Text,
    Image,
    Platform,
    Animated,
    Easing
} from 'react-native';
import * as ImagePicker from 'expo-image-picker';
import { postsAPI, SelectedFile } from '../services/api';

interface CreatePostProps {
    onPostCreated: (postId: string) => void;
}

const CreatePost: React.FC<CreatePostProps> = ({ onPostCreated }) => {
    const [text, setText] = useState('');
    const [files, setFiles] = useState<SelectedFile[]>([]);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    
    // 
    const scaleAnim = useRef(new Animated.Value(1)).current;
    const successOpacityAnim = useRef(new Animated.Value(0)).current;

    const handleAddImage = async () => {
        try {
            const result = await ImagePicker.launchImageLibraryAsync({
                mediaTypes: ImagePicker.MediaTypeOptions.All,
                allowsEditing: true,
                quality: 1,
            });

            if (!result.canceled) {
                const file: SelectedFile = {
                    uri: result.assets[0].uri,
                    type: 'image/jpeg',
                    name: 'photo.jpg'
                };
                setFiles([...files, file]);
            }
        } catch (error) {
            console.error('   :', error);
            setError('   ');
        }
    };

    const animateSuccess = () => {
        //   
        Animated.sequence([
            Animated.timing(scaleAnim, {
                toValue: 0.95,
                duration: 200,
                useNativeDriver: true,
            }),
            //     
            Animated.timing(successOpacityAnim, {
                toValue: 1,
                duration: 200,
                useNativeDriver: true,
            }),
            Animated.delay(1000),
            //   
            Animated.timing(successOpacityAnim, {
                toValue: 0,
                duration: 200,
                useNativeDriver: true,
            }),
            //     
            Animated.timing(scaleAnim, {
                toValue: 1,
                duration: 200,
                useNativeDriver: true,
            })
        ]).start();
    };

    const handleSubmit = async () => {
        if (!text.trim() && files.length === 0) {
            setError('   ');
            return;
        }

        setIsLoading(true);
        setError(null);

        try {
            const postId = await postsAPI.createPost(text, files);
            setText('');
            setFiles([]);
            animateSuccess();
            onPostCreated(postId);
        } catch (error) {
            console.error('   :', error);
            setError(error instanceof Error ? error.message : '   ');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <View style={styles.container}>
            <Animated.View style={[
                styles.formContainer,
                {
                    transform: [{ scale: scaleAnim }]
                }
            ]}>
                <TextInput
                    style={styles.input}
                    placeholder="   ?"
                    placeholderTextColor="#666"
                    value={text}
                    onChangeText={setText}
                    multiline
                    maxLength={5000}
                    editable={!isLoading}
                />
                
                {files.length > 0 && (
                    <View style={styles.filesContainer}>
                        {files.map((file, index) => (
                            <View key={index} style={styles.filePreview}>
                                <Image 
                                    source={{ uri: file.uri }} 
                                    style={styles.imagePreview} 
                                />
                                <TouchableOpacity 
                                    style={styles.removeButton}
                                    onPress={() => setFiles(files.filter((_, i) => i !== index))}
                                >
                                    <Text style={styles.removeButtonText}></Text>
                                </TouchableOpacity>
                            </View>
                        ))}
                    </View>
                )}

                {error && (
                    <Text style={styles.errorText}>{error}</Text>
                )}

                <View style={styles.footer}>
                    <TouchableOpacity 
                        style={styles.addButton} 
                        onPress={handleAddImage}
                        disabled={isLoading}
                    >
                        <Image 
                            source={require('../assets/addfile.png')} 
                            style={styles.addButtonIcon}
                        />
                    </TouchableOpacity>

                    <TouchableOpacity
                        style={[
                            styles.submitButton,
                            (!text.trim() && files.length === 0) && styles.submitButtonDisabled
                        ]}
                        onPress={handleSubmit}
                        disabled={isLoading || (!text.trim() && files.length === 0)}
                    >
                        {isLoading ? (
                            <ActivityIndicator color="#fff" />
                        ) : (
                            <Text style={styles.submitButtonText}></Text>
                        )}
                    </TouchableOpacity>
                </View>
            </Animated.View>

            <Animated.View 
                style={[
                    styles.successMessage,
                    { opacity: successOpacityAnim }
                ]}
                pointerEvents="none"
            >
                <Text style={styles.successText}> !</Text>
            </Animated.View>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        backgroundColor: '#fff',
        borderRadius: 12,
        overflow: 'hidden',
        ...Platform.select({
            ios: {
                shadowColor: '#000',
                shadowOffset: { width: 0, height: 2 },
                shadowOpacity: 0.1,
                shadowRadius: 4,
            },
            android: {
                elevation: 3,
            },
        }),
    },
    formContainer: {
        width: '100%',
        backgroundColor: '#fff',
    },
    input: {
        minHeight: 80,
        padding: 15,
        fontSize: 16,
        color: '#000',
        textAlignVertical: 'top',
    },
    filesContainer: {
        flexDirection: 'row',
        flexWrap: 'wrap',
        padding: 10,
        borderTopWidth: 1,
        borderTopColor: '#eee',
    },
    filePreview: {
        width: 100,
        height: 100,
        margin: 5,
        borderRadius: 8,
        overflow: 'hidden',
        position: 'relative',
    },
    imagePreview: {
        width: '100%',
        height: '100%',
    },
    removeButton: {
        position: 'absolute',
        top: 5,
        right: 5,
        width: 24,
        height: 24,
        borderRadius: 12,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        justifyContent: 'center',
        alignItems: 'center',
    },
    removeButtonText: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 'bold',
    },
    footer: {
        flexDirection: 'row',
        alignItems: 'center',
        padding: 10,
        borderTopWidth: 1,
        borderTopColor: '#eee',
    },
    addButton: {
        width: 40,
        height: 40,
        justifyContent: 'center',
        alignItems: 'center',
        marginRight: 10,
    },
    addButtonIcon: {
        width: 24,
        height: 24,
        tintColor: '#666',
    },
    submitButton: {
        flex: 1,
        height: 40,
        backgroundColor: '#007AFF',
        borderRadius: 20,
        justifyContent: 'center',
        alignItems: 'center',
    },
    submitButtonDisabled: {
        backgroundColor: '#ccc',
    },
    submitButtonText: {
        color: '#fff',
        fontSize: 16,
        fontWeight: '600',
    },
    errorText: {
        color: '#DC3545',
        fontSize: 14,
        marginHorizontal: 15,
        marginBottom: 10,
    },
    successMessage: {
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: 'rgba(255, 255, 255, 0.9)',
    },
    successText: {
        color: '#28A745',
        fontSize: 18,
        fontWeight: '600',
    }
});

export default CreatePost; 