import {
    arrayBufferToPem,
    rsaEncrypt,
    rsaDecrypt,
    generateAESKey,
    blobToUint8Array,
    aesEncrypt,
    aesDecrypt
} from './Crypto';

export interface WebSocketMessage {
    type: string;
    action?: string;
    data?: any;
    status?: string;
    message?: string;
    S_KEY?: string;
    username?: string;
    key?: string;
    accountData?: {
        id: number;
        username: string;
        name: string;
        avatar: string;
    };
    post?: {
        text: string;
        attachments: Array<{
            type: 'image' | 'file';
            file_id: string;
            file_name: string;
            file_size: number;
        }>;
    };
}

class WebSocketService {
    private url: string;
    private socket: WebSocket | null;
    private isConnected: boolean;
    private rsaPublic: ArrayBuffer | null;
    private rsaPrivate: ArrayBuffer | null;
    private rsaPublicServer: string | null;
    private aesKey: string | null;
    private aesServerKey: string | null;
    private keysReady: boolean;
    private socketReady: boolean;
    private messageQueue: WebSocketMessage[];
    private processingMessages: boolean;
    private messageHandlers: Map<string, (data: WebSocketMessage) => void>;

    constructor() {
        // URL WebSocket-
        this.url = 'wss://ws.elemsocial.com/user_api';
        this.socket = null;
        this.isConnected = false;
        this.rsaPublic = null;
        this.rsaPrivate = null;
        this.rsaPublicServer = null;
        this.aesKey = null;
        this.aesServerKey = null;
        this.keysReady = false;
        this.socketReady = false;
        this.messageQueue = [];
        this.processingMessages = false;
        this.messageHandlers = new Map();

        //     
        this.connect();
    }

    get connectionStatus(): boolean {
        return this.isConnected;
    }

    get ready(): boolean {
        return this.socketReady && this.keysReady;
    }

    //  RSA-
    private async generateKeys(): Promise<boolean> {
        const keyPair = await window.crypto.subtle.generateKey({
            name: 'RSA-OAEP',
            modulusLength: 2048,
            publicExponent: new Uint8Array([1, 0, 1]),
            hash: { name: 'SHA-256' }
        }, true, ['encrypt', 'decrypt']);
        this.rsaPublic = await window.crypto.subtle.exportKey('spki', keyPair.publicKey);
        this.rsaPrivate = await window.crypto.subtle.exportKey('pkcs8', keyPair.privateKey);
        this.keysReady = true;
        return true;
    }

    //   
    private processQueue(): void {
        if (!this.socketReady || this.processingMessages) return;
        this.processingMessages = true;
        while (this.messageQueue.length > 0) {
            const message = this.messageQueue.shift();
            if (message) {
                this.send(message);
            }
        }
        this.processingMessages = false;
    }

    //  
    public connect(): void {
        console.log('  WebSocket...');
        this.socket = new WebSocket(this.url);

        this.socket.onopen = async () => {
            console.log('WebSocket ');
            await this.generateKeys();
            if (this.rsaPublic) {
                const publicKeyPem = await arrayBufferToPem(this.rsaPublic, 'PUBLIC KEY');
                this.socket?.send(JSON.stringify({
                    type: 'key_exchange',
                    key: publicKeyPem
                }));
                this.isConnected = true;
                this.processQueue();
            }
        };

        this.socket.onmessage = async (event) => {
            const rawData = event.data;

            if (this.rsaPublicServer) {
                if (this.aesServerKey && this.aesKey) {
                    const decryptedStr = await aesDecrypt(
                        await blobToUint8Array(rawData),
                        this.aesKey
                    );
                    const decryptedData = JSON.parse(decryptedStr) as WebSocketMessage;
                    console.log(' :', decryptedData);
                    
                    //       WebSocket
                    const wsEvent = new CustomEvent('ws_message', { detail: decryptedData });
                    window.dispatchEvent(wsEvent);
                    
                    if (decryptedData.type) {
                        const handler = this.messageHandlers.get(decryptedData.type);
                        if (handler) {
                            handler(decryptedData);
                        }
                    }
                } else {
                    const decryptedStr = await rsaDecrypt(
                        await blobToUint8Array(rawData),
                        this.rsaPrivate!
                    );
                    const decryptedData = JSON.parse(decryptedStr);
                    if (decryptedData.type === 'aes_key') {
                        this.aesServerKey = decryptedData.key;
                        this.socketReady = true;
                        console.log('WebSocket    ');
                        
                        //       
                        this.send({
                            type: 'social',
                            action: 'get_online_users'
                        });
                        
                        this.processQueue();
                    }
                }
            } else {
                const data = JSON.parse(rawData) as WebSocketMessage;
                if (data.type === 'key_exchange') {
                    this.rsaPublicServer = data.key ?? null;
                    this.aesKey = await generateAESKey();
                    console.log('AES  ');
                    if (this.rsaPublicServer) {
                        const encryptedMessage = await rsaEncrypt(
                            JSON.stringify({
                                type: 'aes_key',
                                key: this.aesKey
                            }),
                            this.rsaPublicServer
                        );
                        this.socket?.send(encryptedMessage);
                    }
                }
            }
        };

        this.socket.onclose = () => {
            this.disconnect();
            setTimeout(() => this.connect(), 5000);
        };

        this.socket.onerror = () => {
            this.disconnect();
            setTimeout(() => this.connect(), 5000);
        };
    }

    //  
    public async send(data: WebSocketMessage): Promise<void> {
        if (!this.isConnected || this.socket?.readyState !== WebSocket.OPEN || !this.socketReady) {
            console.log('   :', data);
            this.messageQueue.push(data);
            return;
        }
        const jsonData = JSON.stringify(data);
        if (this.aesServerKey) {
            const encrypted = await aesEncrypt(jsonData, this.aesServerKey);
            if (encrypted) {
                this.socket?.send(encrypted);
            }
        }
    }

    //  
    public disconnect(): void {
        if (this.socket) {
            if (this.socket.readyState === WebSocket.OPEN) {
                this.socket.close();
            }
            this.socket = null;
        }
        this.isConnected = false;
        this.rsaPublic = null;
        this.rsaPrivate = null;
        this.rsaPublicServer = null;
        this.aesKey = null;
        this.aesServerKey = null;
        this.keysReady = false;
        this.socketReady = false;
        console.log('WebSocket ');
    }

    //   
    public addMessageHandler(type: string, handler: (data: WebSocketMessage) => void): void {
        this.messageHandlers.set(type, handler);
    }

    //   
    public removeMessageHandler(type: string): void {
        this.messageHandlers.delete(type);
    }
}

//      
const webSocketService = new WebSocketService();
export default webSocketService; 
