// Types
export interface LoginResponse {
  status: 'success' | 'error';
  S_KEY?: string;
  accountID?: string;
  text?: string;
}

export interface PostResponse {
  ID: string;
  AuthorID: string;
  Username: string;
  Name: string;
  Avatar: string | 'None';
  UserIcons: string | null;
  Text: string;
  Content: string | null;
  Date: string;
  Likes: number;
  Dislikes: number;
  Liked: 'Liked' | null;
  Disliked: 'Liked' | null;
  Comments: number;
  MyPost: boolean;
}

export interface CommentResponse {
  Name: string;
  Username: string;
  Avatar: string | 'None';
  UserIcons: string | null;
  Text: string;
  Date: string;
}

export interface ApiResponse {
  Type: 'Error' | 'Verify';
  Content: string;
}

export interface ProfileData {
  id: number;
  username: string;
  name: string;
  avatar: string | null;
  cover: string | null;
  description: string | null;
  posts: number;
  subscribers: number;
  subscriptions: number;
  subscribed: boolean;
  create_date: string;
  icons: string[];
  links_count: number;
  links: any[];
  my_profile: boolean;
  permissions: string[];
}

export interface FileInfo {
  file_id: string;
  file_name: string;
  file_size: number;
  mime_type: string;
}

export interface SelectedFile {
    uri: string;
    type: string;
    name: string;
}

// Helper functions
export const getImageUrl = async (path: string | null | undefined): Promise<string> => {
  if (!path || path === 'None') {
    return '';
  }
  return `https://elemsocial.com/Content/Avatars/${path}`;
};

export const getAccountData = async () => {
  const accountDataStr = await AsyncStorage.getItem('account_data');
  if (accountDataStr) {
    return JSON.parse(accountDataStr) as WebSocketMessage['accountData'];
  }
  return null;
};

// Auth API
export const authAPI = {
  login: async (email: string, password: string): Promise<LoginResponse> => {
    const formData = new FormData();
    formData.append('email', email);
    formData.append('password', password);
    formData.append('device_type', 'browser');
    formData.append('device', 'Element Web 2.3');

    const response = await fetch(`${AUTH_API_URL}/auth/login`, {
      method: 'POST',
      headers: {
        'api': 'true',
        'Origin': 'https://elemsocial.com',
      },
      body: formData
    });

    const data = await response.json();
    
    if (data.status === 'success' && data.S_KEY) {
      //  S-KEY
      await AsyncStorage.setItem('S-KEY', data.S_KEY);
      
      //   -  
      connectWebSocket().catch(error => {
        console.error('WebSocket setup failed after login:', error);
      });
    }
    
    return data;
  },

  logout: async (): Promise<void> => {
      return new Promise((resolve, reject) => {
        const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('authorization');
        reject(new Error('Logout timeout'));
      }, 5000);

      webSocketService.addMessageHandler('authorization', (data: WebSocketMessage) => {
          clearTimeout(timeout);
        if (data.status === 'success') {
          webSocketService.removeMessageHandler('authorization');
          AsyncStorage.removeItem('S-KEY');
          resolve();
          } else {
          webSocketService.removeMessageHandler('authorization');
          reject(new Error(data.message || 'Failed to logout'));
        }
      });

      webSocketService.send({
        type: 'authorization',
        action: 'logout'
      });
    });
  },

  checkAuth: async (): Promise<boolean> => {
    try {
      await connectWebSocket();
      return true;
    } catch (error) {
      console.error('Auth check failed:', error);
      return false;
    }
  },

  async getMyProfile(): Promise<ProfileData> {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('profile');
        reject(new Error('Profile request timeout'));
      }, 5000);

      webSocketService.addMessageHandler('profile', (data: WebSocketMessage) => {
        clearTimeout(timeout);
        if (data.status === 'success' && data.data) {
          resolve(data.data as ProfileData);
        } else {
          reject(new Error('Failed to get profile data'));
        }
      });

      webSocketService.send({
        type: 'profile',
        action: 'get_my_profile'
      });
    });
  },

  async getProfile(username: string): Promise<ProfileData> {
      return new Promise((resolve, reject) => {
        const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('profile');
          reject(new Error('Profile request timeout'));
      }, 5000);

      webSocketService.addMessageHandler('profile', (data: WebSocketMessage) => {
          clearTimeout(timeout);
        if (data.status === 'success' && data.data) {
          resolve(data.data as ProfileData);
          } else {
          reject(new Error('Failed to get profile data'));
          }
      });
        
      webSocketService.send({
        type: 'profile',
          action: 'get_profile',
        data: { username }
      });
    });
  },

  updateProfile: async (data: Partial<ProfileData>): Promise<ApiResponse> => {
    const formData = new FormData();
    Object.entries(data).forEach(([key, value]) => {
      if (value !== undefined && value !== null) {
        formData.append(key, String(value));
      }
    });
    const response = await postsApi.post<ApiResponse>('/System/API/Connect.php', formData);
    return response.data;
  },

  uploadAvatar: async (file: File): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('avatar', file);
    const response = await postsApi.post<ApiResponse>('/System/API/Connect.php', formData);
    return response.data;
  },

  uploadCover: async (file: File): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('cover', file);
    const response = await postsApi.post<ApiResponse>('/System/API/Connect.php', formData);
    return response.data;
  },
};

// Profile API  WebSocket
export const profileAPI = {
  async getMyProfile(): Promise<ProfileData> {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('social');
        reject(new Error('    '));
      }, 5000);

      webSocketService.addMessageHandler('social', (data: WebSocketMessage) => {
        clearTimeout(timeout);
        if (data.status === 'success' && data.data) {
          webSocketService.removeMessageHandler('social');
          resolve(data.data as ProfileData);
        } else {
          webSocketService.removeMessageHandler('social');
          reject(new Error(data.message || '    '));
        }
      });

      webSocketService.send({
        type: 'social',
        action: 'get_profile'
      });
    });
  },

  async getProfile(username: string): Promise<ProfileData> {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('social');
        reject(new Error('    '));
      }, 5000);

      webSocketService.addMessageHandler('social', (data: WebSocketMessage) => {
        clearTimeout(timeout);
        if (data.status === 'success' && data.data) {
          webSocketService.removeMessageHandler('social');
          resolve(data.data as ProfileData);
        } else {
          webSocketService.removeMessageHandler('social');
          reject(new Error(data.message || '    '));
        }
      });

      webSocketService.send({
        type: 'social',
        action: 'get_profile',
        username: username
      });
    });
  },

  updateProfile: async (data: Partial<ProfileData>): Promise<ApiResponse> => {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        webSocketService.removeMessageHandler('social');
        reject(new Error('Profile update timeout'));
      }, 5000);

      webSocketService.addMessageHandler('social', (response: WebSocketMessage) => {
        clearTimeout(timeout);
        if (response.status === 'success') {
          webSocketService.removeMessageHandler('social');
          resolve({
            Type: 'Verify',
            Content: 'Profile updated successfully'
          });
        } else {
          webSocketService.removeMessageHandler('social');
          resolve({
            Type: 'Error',
            Content: response.message || 'Failed to update profile'
          });
        }
      });

      webSocketService.send({
        type: 'social',
        action: 'update_profile',
        data: data
      });
    });
  },

  uploadAvatar: async (file: File): Promise<ApiResponse> => {
    //   HTTP   
    const formData = new FormData();
    formData.append('avatar', file);
    const response = await postsApi.post<ApiResponse>('/System/API/Connect.php', formData);
    return response.data;
  },

  uploadCover: async (file: File): Promise<ApiResponse> => {
    //   HTTP   
    const formData = new FormData();
    formData.append('cover', file);
    const response = await postsApi.post<ApiResponse>('/System/API/Connect.php', formData);
    return response.data;
  },
};

// Posts API
export const postsAPI = {
  // Load single post
  loadPost: async (postId: string): Promise<PostResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post('/System/API/LoadPost.php', formData);
    return response.data;
  },

  // Create post
  createPost: async (text: string, files: SelectedFile[]): Promise<string> => {
    const formData = new FormData();
    formData.append('Text', text);
    
    //  ,   
    files.forEach(file => {
        formData.append('Files[]', {
            uri: file.uri,
            type: file.type,
            name: file.name
        } as any);
    });

    //   ,    
    const hasImages = files.some(file => file.type.startsWith('image/'));
    if (hasImages) {
        formData.append('ClearMetadataIMG', 'false');
        formData.append('CensoringIMG', 'false');
    }

    const response = await postsApi.post<{Type: string, Content: string}>('/System/API/AddPost.php', formData);
    
    if (response.data.Type === 'Verify') {
        return response.data.Content; // ID  
    } else {
        throw new Error(response.data.Content);
    }
  },

  // Get single post
  getPost: async (postId: string): Promise<PostResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<PostResponse>('/System/API/LoadPost.php', formData);
    return response.data;
  },

  getPosts: async (type: 'LATEST' | 'REC' | 'SUBSCRIPTIONS' | 'USER', startIndex: number = 0, userId?: string): Promise<PostResponse[]> => {
    try {
      const formData = new FormData();
      formData.append('StartIndex', startIndex.toString());
      
      if (type === 'USER' && userId) {
        formData.append('UserID', userId);
      }

      const url = `/System/API/LoadPosts.php?F=${type}`;
      const response = await postsApi.post(url, formData);
      
      //  
      let postsArray: any[] = [];
      
      if (typeof response.data === 'string') {
        try {
          postsArray = JSON.parse(response.data);
        } catch (parseError) {
          console.error('Error parsing posts data:', parseError);
          return [];
        }
      } else if (Array.isArray(response.data)) {
        postsArray = response.data;
      } else if (response.data && typeof response.data === 'object') {
        postsArray = [response.data];
      }

      //   
      return postsArray.map(post => ({
        ID: String(post.ID || ''),
        AuthorID: String(post.AuthorID || ''),
        Username: String(post.Username || ''),
        Name: String(post.Name || ''),
        Avatar: post.Avatar || 'None',
        UserIcons: post.UserIcons || null,
        Text: String(post.Text || ''),
        Content: post.Content || null,
        Date: String(post.Date || ''),
        Likes: Number(post.Likes) || 0,
        Dislikes: Number(post.Dislikes) || 0,
        Liked: post.Liked || null,
        Disliked: post.Disliked || null,
        Comments: Number(post.Comments) || 0,
        MyPost: Boolean(post.MyPost)
      }));
    } catch (error) {
      console.error('Error fetching posts:', error);
      if (axios.isAxiosError(error)) {
        console.error('Response:', error.response?.data);
      }
      return [];
    }
  },

  // Post interactions (like, dislike, delete)
  interactWithPost: async (
    action: 'LIKE' | 'DISLIKE' | 'DELETE',
    postId: string
  ): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<ApiResponse>(`/System/API/PostInteraction.php?F=${action}`, formData);
    return response.data;
  },

  // Add comment to post
  addComment: async (postId: string, text: string): Promise<ApiResponse> => {
    const formData = new FormData();
    formData.append('PostID', postId);
    formData.append('Text', text);

    const response = await postsApi.post<ApiResponse>('/System/API/PostInteraction.php?F=POST_COMMENT', formData);
    return response.data;
  },

  // Get post comments
  getComments: async (postId: string): Promise<CommentResponse[]> => {
    const formData = new FormData();
    formData.append('PostID', postId);

    const response = await postsApi.post<CommentResponse[]>('/System/API/PostInteraction.php?F=LOAD_COMMENT', formData);
    return response.data;
  },

  //  getUserPosts,       getPosts
  getUserPosts: async (userId: string, startIndex: number = 0): Promise<PostResponse[]> => {
    return postsAPI.getPosts('USER', startIndex, userId);
  },
};
