//    ArrayBuffer  PEM 
export async function arrayBufferToPem(buffer: ArrayBuffer, type: string): Promise<string> {
    const binary = String.fromCharCode.apply(null, Array.from(new Uint8Array(buffer)));
    const base64 = btoa(binary);
    let pem = `-----BEGIN ${type}-----\n`;
    pem += base64.match(/.{1,64}/g)?.join('\n') || '';
    pem += `\n-----END ${type}-----\n`;
    return pem;
}

//   
export function importPublicKey(pk: string): ArrayBuffer {
    const base64String = pk
        .replace(/-----BEGIN PUBLIC KEY-----/, '')
        .replace(/-----END PUBLIC KEY-----/, '')
        .replace(/\s/g, '');
    const publicKeyBytes = Uint8Array.from(atob(base64String), c => c.charCodeAt(0));
    return publicKeyBytes.buffer;
}

//   
export function importPrivateKey(pk: string): ArrayBuffer {
    const base64String = pk
        .replace(/-----BEGIN PRIVATE KEY-----/, '')
        .replace(/-----END PRIVATE KEY-----/, '')
        .replace(/\s/g, '');
    const privateKeyBytes = Uint8Array.from(atob(base64String), c => c.charCodeAt(0));
    return privateKeyBytes.buffer;
}

// RSA 
export async function rsaEncrypt(data: string, pk: string): Promise<Uint8Array> {
    try {
        const publicKeyBuffer = importPublicKey(pk);
        const dataBuffer = new TextEncoder().encode(data);
        const publicKeyE = await crypto.subtle.importKey(
            'spki',
            publicKeyBuffer,
            { name: 'RSA-OAEP', hash: { name: 'SHA-256' } },
            true,
            ['encrypt']
        );
        const encryptedDataBuffer = await crypto.subtle.encrypt(
            { name: 'RSA-OAEP' },
            publicKeyE,
            dataBuffer
        );
        return new Uint8Array(encryptedDataBuffer);
    } catch (error) {
        throw new Error('  : ' + (error as Error).message);
    }
}

// RSA 
export async function rsaDecrypt(data: Uint8Array, privateKey: ArrayBuffer): Promise<string> {
    try {
        const privateKeyE = await crypto.subtle.importKey(
            'pkcs8',
            privateKey,
            { name: 'RSA-OAEP', hash: { name: 'SHA-256' } },
            true,
            ['decrypt']
        );
        const decryptedDataBuffer = await crypto.subtle.decrypt(
            { name: 'RSA-OAEP' },
            privateKeyE,
            data
        );
        return new TextDecoder().decode(decryptedDataBuffer);
    } catch (error) {
        throw new Error('  : ' + (error as Error).message);
    }
}

//   AES (32 ,   Base64)
export function generateAESKey(): string {
    const bytes = crypto.getRandomValues(new Uint8Array(32));
    return btoa(String.fromCharCode(...Array.from(bytes)));
}

//  Base64  ArrayBuffer
export function base64ToBytes(base64: string): ArrayBuffer {
    const binaryString = atob(base64);
    const bytes = new Uint8Array([...binaryString].map(char => char.charCodeAt(0)));
    return bytes.buffer;
}

// AES 
export async function aesEncrypt(data: string, key: string): Promise<Uint8Array | null> {
    const iv = crypto.getRandomValues(new Uint8Array(16));
    const encodedData = new TextEncoder().encode(data);

    try {
        const importedKey = await crypto.subtle.importKey(
            'raw',
            base64ToBytes(key),
            { name: 'AES-CBC' },
            false,
            ['encrypt']
        );
        const encryptedBuffer = await crypto.subtle.encrypt(
            { name: 'AES-CBC', iv: iv },
            importedKey,
            encodedData
        );

        //  IV      
        const result = new Uint8Array(iv.byteLength + encryptedBuffer.byteLength);
        result.set(iv);
        result.set(new Uint8Array(encryptedBuffer), iv.byteLength);
        return result;
    } catch (error) {
        console.error("  :", error);
        return null;
    }
}

// AES 
export async function aesDecrypt(data: Uint8Array, key: string): Promise<string> {
    const iv = data.slice(0, 16);
    const encrypted = data.slice(16);

    const importedKey = await crypto.subtle.importKey(
        'raw',
        base64ToBytes(key),
        { name: 'AES-CBC' },
        false,
        ['decrypt']
    );
    const decryptedBuffer = await crypto.subtle.decrypt(
        { name: 'AES-CBC', iv: iv },
        importedKey,
        encrypted
    );
    return new TextDecoder().decode(decryptedBuffer);
}

//  Blob  Uint8Array
export async function blobToUint8Array(blob: Blob): Promise<Uint8Array> {
    const arrayBuffer = await blob.arrayBuffer();
    return new Uint8Array(arrayBuffer);
}

//  ArrayBuffer  Base64
export function arrayBufferToBase64(buffer: ArrayBuffer): string {
    const bytes = new Uint8Array(buffer);
    let binary = '';
    for (let i = 0; i < bytes.byteLength; i++) {
        binary += String.fromCharCode(bytes[i]);
    }
    return btoa(binary);
}

//  Base64  ArrayBuffer
export function base64ToArrayBuffer(base64: string): ArrayBuffer {
    const binaryString = atob(base64);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; ++i) {
        bytes[i] = binaryString.charCodeAt(i);
    }
    return bytes.buffer;
} 