import React, { useState, useEffect } from 'react';
import { View, Text, ScrollView, StyleSheet, Image, TouchableOpacity } from 'react-native';
import { useTheme } from '@react-navigation/native';
import { OnlineUser } from '../types/websocket';
import webSocketService from '../services/WebSocket';
import LetterAvatar from './LetterAvatar';
import ProfileModal from './ProfileModal';

const OnlineUsers: React.FC = () => {
    const [onlineUsers, setOnlineUsers] = useState<OnlineUser[]>([]);
    const [selectedUser, setSelectedUser] = useState<OnlineUser | null>(null);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const { colors } = useTheme();

    useEffect(() => {
        const handleSocialMessage = (data: any) => {
            if (data.type === 'social' && data.action === 'get_online_users' && Array.isArray(data.users)) {
                //     
                const formattedUsers = data.users.map((user: any) => ({
                    ID: user.id.toString(),
                    AuthorID: user.id.toString(),
                    Username: user.username,
                    Name: user.name,
                    Avatar: user.avatar
                }));
                setOnlineUsers(formattedUsers);
            }
        };

        //    
        webSocketService.addMessageHandler('social', handleSocialMessage);

        //    
        webSocketService.send({
            type: 'social',
            action: 'get_online_users'
        });

        return () => {
            webSocketService.removeMessageHandler('social');
        };
    }, []);

    const handleUserPress = (user: OnlineUser) => {
        setSelectedUser(user);
        setIsModalVisible(true);
    };

    return (
        <View style={styles.container}>
            <Text style={[styles.title, { color: colors.text }]}>  </Text>
            <ScrollView 
                horizontal 
                showsHorizontalScrollIndicator={false}
                style={styles.scrollView}
            >
                {onlineUsers.length > 0 ? (
                    onlineUsers.map((user) => (
                        <TouchableOpacity 
                            key={user.ID} 
                            style={styles.userContainer}
                            onPress={() => handleUserPress(user)}
                        >
                            <View style={styles.avatarContainer}>
                                {user.Avatar && user.Avatar !== 'None' ? (
                                    <Image 
                                        source={{ uri: `https://elemsocial.com/Content/Avatars/${user.Avatar}` }} 
                                        style={styles.avatar}
                                    />
                                ) : (
                                    <LetterAvatar 
                                        name={user.Name || user.Username} 
                                        size={60}
                                    />
                                )}
                                <View style={styles.onlineIndicator} />
                            </View>
                            <Text style={[styles.username, { color: colors.text }]} numberOfLines={1}>
                                {user.Name || user.Username}
                            </Text>
                        </TouchableOpacity>
                    ))
                ) : (
                    <View style={styles.emptyContainer}>
                        <Text style={[styles.emptyText, { color: colors.text }]}>
                            ...
                        </Text>
                    </View>
                )}
            </ScrollView>

            {selectedUser && (
                <ProfileModal
                    visible={isModalVisible}
                    onClose={() => setIsModalVisible(false)}
                    username={selectedUser.Username}
                />
            )}
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        marginVertical: 10,
        borderBottomWidth: 1,
        borderBottomColor: '#eee',
        paddingBottom: 15,
    },
    title: {
        fontSize: 16,
        fontWeight: '600',
        marginLeft: 15,
        marginBottom: 10,
    },
    scrollView: {
        paddingHorizontal: 10,
    },
    userContainer: {
        alignItems: 'center',
        marginHorizontal: 5,
        width: 70,
    },
    avatarContainer: {
        position: 'relative',
        width: 60,
        height: 60,
    },
    avatar: {
        width: 60,
        height: 60,
        borderRadius: 30,
        backgroundColor: '#f0f0f0',
    },
    onlineIndicator: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        width: 14,
        height: 14,
        borderRadius: 7,
        backgroundColor: '#4CAF50',
        borderWidth: 2,
        borderColor: '#FFFFFF',
    },
    username: {
        fontSize: 12,
        marginTop: 5,
        textAlign: 'center',
    },
    emptyContainer: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        paddingHorizontal: 20,
    },
    emptyText: {
        fontSize: 14,
        color: '#666',
    },
});

export default OnlineUsers; 